import request from "@/utils/request.js";

// 保存地址
export function goAddress(data, token) {
    return request({
        url: '/address',
        method: 'post',
        data: data,
        headers: { "x-token": token }
    })
}

// 获取地址
export function getAddress(token) {
    return request({
        url: '/address',
        method: 'get',
        headers: { "x-token": token }
    })
}